import * as actionTypes from './actionTypes';

//immutablejsimmutablestate
import {fromJS,List} from 'immutable';

const defaultState=fromJS({
    checklist:[
        {name:'',checked:true},
        {name:'',checked:true},
        {name:'',checked:true},
        {name:'',checked:true},
        {name:'',checked:true},
        {name:'',checked:true},
    ],
    inputValue:'',
    eventList:[],
    isShowResult:false, // 
    // test:false
});

export default (state=defaultState,action)=>{
    switch (action.type) {
        case actionTypes.CHANGE_CHECKED:
            let oldChecklist=state.get('checklist'); // 
            let changeIndexMap=oldChecklist.get(action.index); //map {name:'',checked:false}
            let checkedValue=changeIndexMap.get('checked'); // map
            return state.set('checklist',
                oldChecklist.set(action.index,
                    changeIndexMap.set('checked',!checkedValue)));
        case actionTypes.CHANGE_INPUT_VALUE:
            return state.set('inputValue',action.value);
        case actionTypes.INIT_LIST:
            return state.set('eventList',action.list);
        case actionTypes.RERENDER:
            return state.merge({
                isShowResult:true,
                inputValue:''
            });
/*        case 'test':
            let test=state.get('test');
            return state.set('test',!test);*/
    }

     return state;
}
